import { reactive, watch, ref, onMounted } from 'vue';
import OrderApprovalCell from '@/main-module/management/workbench/view/OrderApprovalCell';
export default {
  name: 'ManageProjectsCell',
  components: {
    OrderApprovalCell
  },
  props: ['itemCount', 'selectedIndex', 'modifyIndex'],

  setup(props, context) {
    const watchProps = reactive(props);
    const scrollView = ref(null);
    const container = ref(null);
    const scrollData = reactive({
      pageIndex: 0,
      startX: 0,
      distanceX: 0,
      isMove: false
    });

    const setTranslateX = transX => {
      container.value.style.transition = 'all .25s';
      container.value.style.transform = `translateX(${transX}px)`;
    };

    const bindEvent = () => {
      const pageWidth = scrollView.value.offsetWidth;
      const items = container.value.children;
      container.value.addEventListener('touchstart', function (event) {
        scrollData.startX = event.touches[0].clientX;
      }, false);
      container.value.addEventListener('touchmove', function (event) {
        const moveX = event.touches[0].clientX;

        if (moveX > scrollData.startX && scrollData.pageIndex === 0 || moveX < scrollData.startX && scrollData.pageIndex === items.length - 1) {
          return;
        }

        scrollData.distanceX = moveX - scrollData.startX;
        const transX = -pageWidth * scrollData.pageIndex + scrollData.distanceX;
        container.value.style.transition = '';
        container.value.style.transform = `translateX(${transX}px)`;
        scrollData.isMove = true;
      }, false);
      container.value.addEventListener('touchend', function (event) {
        if (!scrollData.isMove) {
          return;
        }

        if (Math.abs(scrollData.distanceX) >= pageWidth / 3) {
          if (scrollData.distanceX > 0) {
            scrollData.pageIndex--;
          }

          if (scrollData.distanceX < 0) {
            scrollData.pageIndex++;
          }
        }

        setTranslateX(-scrollData.pageIndex * pageWidth);
        props.modifyIndex(scrollData.pageIndex);
        scrollData.isMove = false;
        scrollData.distanceX = 0;
        scrollData.startX = 0;
      }, false);
    };

    onMounted(() => {
      bindEvent();
      scrollData.pageIndex = props.selectedIndex;
      setTranslateX(-scrollData.pageIndex * scrollView.value.offsetWidth);
    });
    watch(watchProps, (newValue, oldValue) => {
      if (scrollData.pageIndex === props.selectedIndex) {
        return;
      }

      scrollData.pageIndex = props.selectedIndex;
      setTranslateX(-scrollData.pageIndex * scrollView.value.offsetWidth);
    }, true);
    return {
      scrollView,
      container,
      scrollData,
      scrollTo
    };
  }

};