import { useRouter } from 'vue-router';
import { useStore } from 'vuex';
import { computed } from 'vue';
export default {
  name: 'CommonlyUsedCell',

  setup() {
    const store = useStore();
    const myApp = computed(() => {
      const array = [...store.state.workbench.myApp];
      const allApp = {
        title: '全部应用',
        icon: require('@/assets/management_img/ico-应用-全部应用@2x.png'),
        name: 'allModule'
      };

      if (array.length >= 8) {
        array.splice(7);
      }

      array.push(allApp);
      return array;
    });
    const router = useRouter();

    const itemEvent = item => {
      if (item.name) {
        router.push({
          name: item.name
        });
      }
    };

    return {
      itemEvent,
      myApp
    };
  }

};